import React, { Component } from 'react';
import {
    Widget,
    addResponseMessage,
    renderCustomComponent,
    addLinkSnippet,
    getCustomLauncher,
    Launcher,
    toggle,
    toggleInputDisabled,
toggleWidget,
dropMessages,

} from 'react-chat-widget';

import { ApiAiClient } from "./ApiAi";
import 'react-chat-widget/lib/styles.css';
import './App.css';
var logo = require("/home/starsystems/Desktop/chatbot/src/logo.svg")


const accessToken1 = '45b1c2e61bf447cfbf344c2c3e806644';
const accessToken2 = 'c6e34b31d0ce4dcaa54e62f9e21ea4b2';

var awesomeLink = {title:"Awesome page", link:"https://www.google.com/"};
class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            // accessToken: accessToken1
        }
        this.apiAiClient1 = new ApiAiClient({ accessToken : accessToken1 });
        this.apiAiClient2 = new ApiAiClient({ accessToken : accessToken2 });
        this.handleresponse = this.handleresponse.bind(this);
        this.start = this.start.bind(this);
        }
  
   componentWillMount() {
   // toggleWidget();
    addResponseMessage("Hi");
    addLinkSnippet(awesomeLink)
    // toggleInputDisabled()
}
start(){
    // toggleWidget()
    document.getElementsByClassName("App")[0].style.display="block";
    
    fetch('',{
        method:'POST',
        headers:{
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        },
        
    }).then((Response) => Response.json())
        .catch(error => console.error('Error:', error))
        .then((response) => console.log(response));
}
    handleNewUserMessage = (newMessage) => {
        this.apiAiClient1.textRequest(newMessage).then((response) => {
            if(response.result.fulfillment.speech != ""){
                this.handleresponse(response);
            }
            else{
                this.apiAiClient2.textRequest(newMessage).then((response) => {
                    this.handleresponse(response);
                })
            }
         

        });
    }

    handleresponse = (response) => {
          
        var speech = response.result.fulfillment.speech;
        if(speech == ""){
            this.setState({
                accessToken:accessToken2
            })
            this.handleNewUserMessage()
        }
        if(speech == "Sorry, what was that?"  || speech == "Say that again?" || speech== "I didn't get that."){
            this.dropMessages()
        }
        //method to display the response message
        addResponseMessage(speech);
    }

    render() {

        return ( 
            <div className="start">
             <button onClick={
                 this.start
                    // var x = document.getElementsByClassName("App")[0];
                    // if (x.style.display === "none") {
                    //     x.style.display = "block";
                    // } else {
                    //     x.style.display = "none";
                    // }
                 
             }> Chat</button>

            < div className = "App" >
           
           < Widget title = { "Bot" }
           autofocus = {true}
            subtitle = ""
            senderPlaceHolder = ""
            profileAvatar={logo}
            titleAvatar = {logo}
            showCloseButton = { true }
            renderCustomComponent = { this.renderCustomComponent }
            handleNewUserMessage = { this.handleNewUserMessage }
            launcher={this.getCustomLauncher}
            
           
            /> 
           </div>
           </div>
        );
    }
}

export default App;



